import React from 'react';
import Input from '../../atoms/Input';

const getFilterValue = (key, filterObject, handleFilterChange) => {
	return filterObject && filterObject.map((value) => {
		return (
			<div className="truncate">
				<Input
					type="checkbox"
					id={`${key}-${value}`}
					onChange={handleFilterChange}
					name={value}
				/>
				<label for={value} className="search-checkbox">{value}</label><br />
			</div>
		)
	})
}

const Filter = ({
	filterObject,
	key,
	handleFilterChange,
}) => {
	console.log(handleFilterChange);
	return (
		<div className="filter-border">
			<div className="filter-keys">
					{key}
			</div>
				{getFilterValue(key, filterObject[key], handleFilterChange)}
		</div>
	)
}

export default Filter;